import $ from 'jquery';

import BpmnModeler from 'bpmn-js/lib/Modeler';

import diagramXML from '../resources/newDiagram.bpmn';

import resizeAllModule from 'bpmn-js-nyan/lib/resize-all-rules';

import minimapModule from 'diagram-js-minimap';

import BpmnColorPickerModule from 'bpmn-js-color-picker';

var container = $('#js-drop-zone');
var downloadLink = $('#js-download-diagram');
var downloadSvgLink = $('#js-download-svg');
var saveXMLDiagram = $('#js-save-diagram');
var name2 = 'new_diagram.bpmn';

var modeler = new BpmnModeler({
  additionalModules: [
    resizeAllModule,
    minimapModule,
    BpmnColorPickerModule
  ],
  container: '#js-canvas',
  propertiesPanel: {
    parent: '#js-properties-panel'
  },
  keyboard: {
    bindTo: window
  }
});

function createNewDiagram() {
  openDiagram(diagramXML);
}

async function openDiagram(xml) {
  try {

    await modeler.importXML(xml);

    modeler.get('minimap').open();
    container
      .removeClass('with-error')
      .addClass('with-diagram');
  } catch (err) {

    container
      .removeClass('with-diagram')
      .addClass('with-error');

    container.find('.error pre').text(err.message);

    console.error(err);
  }
}

$.ajaxSetup({
  crossDomain: true,
  xhrFields: {
    withCredentials: true
  },
});

$.ajax({
  method: 'GET',
  url: 'https://tulavcube.ru:25880/diagrams',
})
  .done(function(msg) {
    msg.diagrams.forEach(addButtons);
  })
  .fail(function() {});

function addButtons(item) {
  var li = document.createElement('li');
  li.className = 'nav-item';

  var button = document.createElement('button');
  button.type = 'button';
  button.style.cssText = 'width: 100%';
  button.className = 'btn btn-outline-primary';
  button.id = item;
  button.ariaCurrent = 'page';
  button.textContent = item;

  document.getElementById('schem-list').appendChild(li);
  li.appendChild(button);

  document.getElementById(item).addEventListener('click', function() {
    getDiagram(item);
  }, false);
}

function getDiagram(id) {
  name2 = id;
  $.ajax({
    method: 'GET',
    url: 'https://tulavcube.ru:25880/diagrams',
    data: { name: id },
  })
    .done(function(msg) {
      openDiagram(msg);
    });
}

function registerFileDrop(container, callback) {

  function handleFileSelect(e) {
    e.stopPropagation();
    e.preventDefault();

    var files = e.dataTransfer.files;

    var file = files[0];

    var reader = new FileReader();

    reader.onload = function(e) {

      var xml = e.target.result;

      callback(xml);
    };

    reader.readAsText(file);
  }

  function handleDragOver(e) {
    e.stopPropagation();
    e.preventDefault();

    e.dataTransfer.dropEffect = 'copy'; // Explicitly show this is a copy.
  }

  container.get(0).addEventListener('dragover', handleDragOver, false);
  container.get(0).addEventListener('drop', handleFileSelect, false);
}

modeler.on('commandStack.changed', () => {

});

modeler.on('element.changed', (event) => {
  $.post('https://tulavcube.ru:25880/element_changed', { element: JSON.stringify(event.element) });
  return false;
});


// file drag / drop ///////////////////////

// check file api availability
if (!window.FileList || !window.FileReader) {
  window.alert(
    'Looks like you use an older browser that does not support drag and drop. ' +
    'Try using Chrome, Firefox or the Internet Explorer > 10.');
} else {
  registerFileDrop(container, openDiagram);
}

// bootstrap diagram functions

$(function() {

  $('#js-create-diagram').click(function(e) {
    e.stopPropagation();
    e.preventDefault();
    window.$('#diagramModal').modal('hide');
    name2 = $('#diagram-name').val();

    createNewDiagram();
  });

  $('.buttons a').click(function(e) {
    if (!$(this).is('.active')) {
      e.preventDefault();
      e.stopPropagation();
    }
  });

  function setEncoded(link, name, data) {
    var encodedData = encodeURIComponent(data);

    if (data) {
      link.removeClass('disabled');
      link.addClass('active').attr({
        'href': 'data:application/bpmn20-xml;charset=UTF-8,' + encodedData,
        'download': name
      });
    } else {
      link.removeClass('active');
      link.addClass('disabled');
    }
  }

  function saveDiagram(link, id, xml) {
    $('#js-save-diagram').click(function(e) {
      $.post('https://tulavcube.ru:25880/diagrams', { name: id, content: xml });
    });

    if (xml) {
      link.removeClass('disabled');
      link.addClass('active').removeAttr('href');
    } else {
      link.removeClass('active');
      link.addClass('disabled');
    }
    return false;
  }

  var exportArtifacts = debounce(async function() {
    try {

      const { svg } = await modeler.saveSVG();

      setEncoded(downloadSvgLink, name2 + '.svg', svg);
    } catch (err) {

      console.error('Error happened saving svg: ', err);
      setEncoded(downloadSvgLink, name2 + '.svg', null);
    }

    try {

      const { xml } = await modeler.saveXML({ format: true });
      setEncoded(downloadLink, name2 + '.bpmn', xml);
    } catch (err) {

      console.error('Error happened saving XML: ', err);
      setEncoded(downloadLink, name2 + '.bpmn', null);
    }

    try {

      const { xml } = await modeler.saveXML({ format: true });

      saveDiagram(saveXMLDiagram, name2, xml);

    } catch (err) {

      console.error('Error happened saving XML: ', err);
    }
  }, 500);

  modeler.on('commandStack.changed', exportArtifacts);
});



// helpers //////////////////////

function debounce(fn, timeout) {

  var timer;

  return function() {
    if (timer) {
      clearTimeout(timer);
    }

    timer = setTimeout(fn, timeout);
  };
}
